
body {
    padding: 4.4rem;
}

h1 {
    padding-left: 0.5rem;
}

small {
    color:#dd2b2b
}

.list-group-item {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

p{
    cursor:pointer;
    margin: 0;
}

.complete{
    text-decoration: line-through;
}

@media screen and (max-width: 570px) {
    
    body {
        padding: 2rem;
    }

    .row {
        display: flex;
        flex-direction: column;
    }
    
    .col-7 {
        order: 1;
        width: 100%;
    }
    
    .col-5{
        margin-bottom: 1rem;
        width: 100%;
    }
    h1 {
        padding-left: 0;
    }
    
}